@import "../../global.scss";

.client-panel {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  overflow: hidden;

  .client-aside {
    width: 320px;
    background-color: #eaeef1;
    padding: 20px;
    transition: width 0.3s ease;
    overflow-x: hidden;
    margin-top: 100px;
    overflow-y: auto;

    &--open {
      width: 320px;
    }

    &--closed {
      width: 90px;
      overflow: hidden;

      .client-aside__title,
      .client-aside__desc {
        display: none;
      }

      .client-aside__list .client-aside__link {
        justify-content: center;
        padding: 8px;
      }
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: 1rem;

      .client-aside__toggle {
        background: none;
        border: none;
        font-size: 1.3rem;
        cursor: pointer;
        color: black;
        margin-right: 10px;
      }
    }

    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-track {
      background: #34495e;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #7f8c8d;
      border-radius: 4px;

      &:hover {
        background-color: #95a5a6;
      }
    }

    &__title {
      font-size: 20px;
      font-weight: 700;
      margin-bottom: 0.5rem;
      background-color: #d9d9d9;
      color: black;
      padding: 10px;
      border-radius: 5px;

      @media (max-width: 1520px) {
        display: none;
      }
    }

    &__desc {
      font-size: 18px;
      margin-bottom: 20px;
      font-weight: 700;
      text-align: center;
      color: black;
    }

    &__nav {
      background-color: #dfe1e3;
      border-radius: 5px;
      padding: 10px;
      min-width: 70px;

      .client-aside__list {
        list-style: none;
        margin: 0;
        padding: 0;
      }
    }

    &__list {
      list-style: none;
      padding: 0;
    }

    &__link {
      text-decoration: none;
      color: black;
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      padding: 12px;
      border-radius: 8px;
      background-color: #ffffff00;
      transition: all 0.2s ease;

      &--back {
        width: 100%;
        font-size: 1.6rem;

        &:hover {
          background-color: #3498db;
          color: $realwhite;
          transform: translateX(5px);
        }
      }

      &-icon {
        margin-right: 10px;
        font-size: 1.2rem;
      }

      .icon {
        &-dashboard {
          color: #83ff00;
        }

        &-orders {
          color: #e74c3c;
        }

        &-complaints {
          color: #f39c12;
        }

        &-probabilistic {
          color: #8e44ad;
        }

        &-account {
          color: #2ecc71;
        }
      }

      &:hover {
        background-color: #3498db;
        color: $realwhite;
        transform: translateX(5px);
      }

      &--active {
        color: black;
        background-color: #2b7fb8;
        font-weight: 700;
        border-radius: 8px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
      }

      &--logout {
        margin-top: 1rem;
        margin-left: 10px;
        padding: 1rem 2rem;
        font-size: 1.5rem;
        background-color: #dc3545;
        border: 1px solid #dc3545;
        color: $realwhite;

        &:hover {
          background-color: black;
          border: 1px solid #dc3545;
          color: #dc3545;
        }
      }
    }
  }

  .client-wrapper {
    flex-grow: 1;
    overflow-y: auto;
    overflow-x: hidden;
    background: whitesmoke;
    padding: 30px;

    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-track {
      background: #eff3f4;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #c0c0c0;
      border-radius: 4px;

      &:hover {
        background-color: #a0a0a0;
      }
    }

    .client-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 2px solid #eff3f4;
      padding: 0 50px;
      margin-top: 100px;

      &__title {
        color: black;
      }

      &__user {
        display: flex;
        align-items: center;

        &-av {
          width: 40px;
          height: 40px;
          border-radius: 50%;
        }

        &-hello {
          margin-left: 10px;
          color: #6f7d83;
        }
      }
    }

    .client-main {
      padding: 30px;
      max-width: 1200px;
      margin: 0 auto;

      @media (max-width: 1520px) {
        padding: 0px;
      }

      @media (max-width: 768px) {
        padding: 15px;
      }

      .form-container {
        max-width: 600px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        gap: 1.5rem;

        label {
          font-size: 1rem;
          font-weight: bold;
          margin-bottom: 0.5rem;
        }

        input,
        select {
          width: 100%;
          padding: 10px;
          border: 1px solid #ccc;
          border-radius: 4px;
          font-size: 1rem;
        }

        button {
          background-color: #198754;
          color: $realwhite;
          padding: 10px 20px;
          border: none;
          border-radius: 4px;
          font-size: 1rem;
          cursor: pointer;
          transition: background-color 0.3s;

          &:hover {
            background-color: #157347;
          }
        }
      }
    }

    .small-box {
      color: $realwhite;
      padding: 20px;
      border-radius: 5px;
      margin-bottom: 20px;
      text-align: center;

      &.bg-primary {
        background-color: #0d6efd;
      }

      &.bg-danger {
        background-color: #dc3545;
      }

      .inner {
        h3 {
          font-size: 2.5rem;
          font-weight: bold;
        }

        p {
          font-size: 1.2rem;
        }
      }
    }
  }
}

.table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 1.5rem;

  th,
  td {
    border: 1px solid #ccc;
    padding: 12px;
    text-align: left;
  }

  th {
    background-color: #d9e7f0;
    font-weight: bold;
  }

  tbody tr:hover {
    background-color: #f2f2f2;
  }
}

.status {
  &-pending {
    color: rgb(116, 116, 35);
    font-weight: bold !important;

    &-badge {
      background-color: rgba(119, 0, 255, 0.2);
      color: #f9e4b7;
    }
  }

  &-resolved {
    color: green;
    font-weight: bold !important;

    &-badge {
      background-color: rgba(119, 0, 255, 0.2);
      color: #00ff40;
    }
  }

  &-rejected {
    color: red;
    font-weight: bold !important;

    &-badge {
      background-color: rgba(119, 0, 255, 0.2);
      color: #ff0000;
    }
  }

  &-processing {
    color: blue;
    font-weight: bold !important;

    &-badge {
      background-color: rgba(119, 0, 255, 0.2);
      color: #fef5db;
    }
  }

  &-shipped {
    color: orange;
    font-weight: bold !important;

    &-badge {
      background-color: rgba(119, 0, 255, 0.2);
      color: #d1f7d1;
    }
  }

  &-delivered {
    color: green;
    font-weight: bold !important;

    &-badge {
      background-color: rgba(119, 0, 255, 0.2);
      color: #c2f0c2;
    }
  }

  &-cancelled {
    color: red;
    font-weight: bold !important;

    &-badge {
      background-color: rgba(119, 0, 255, 0.2);
      color: #f5d0b5;
    }
  }
}

.status-pending,
.status-rejected,
.status-resolved,
.status-processing,
.status-shipped,
.status-delivered,
.status-cancelled {
  background-color: rgba(119, 0, 255, 0.2);
  display: inline-block;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 1.2rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.btn-main-client {
  border: 1px solid #007bff !important;
  background-color: #007bff;
  color: $realwhite;

  &:hover {
    color: #007bff !important;
    background-color: #fff !important;
  }
}

.text-align-client {
  text-align: center !important;
}

.table-center {
  display: flex;
  justify-content: center;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;

  button {
    background-color: #198754;
    color: $realwhite;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
    max-width: 150px;

    &:hover {
      background-color: #157347;
    }
  }
}

.product-table {
  &-container2 {
    background: linear-gradient(135deg, #eaeef1, #dfe1e3);
    border-radius: 16px;
    padding: 2rem;
    margin-bottom: 2.5rem;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.08);
    backdrop-filter: blur(10px);
  }

  &-header2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);

    h2 {
      font-size: 2rem;
      font-weight: 600;
      color: black;
    }

    @media (max-width: 640px) {
      flex-direction: column;
      align-items: flex-start;
      gap: 1rem;
    }
  }

  &2 {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;

    th,
    td {
      padding: 1rem;
      text-align: left;
    }

    thead {
      background-color: rgb(206 208 211 / 60%);
      color: black;
    }

    th {
      font-weight: 600;
      color: black;
      text-transform: uppercase;
      font-size: 1.5rem;
      letter-spacing: 0.05em;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      position: sticky;
      top: 0;
      z-index: 10;
    }

    .sortable-header {
      cursor: pointer;

      &:hover {
        background-color: rgba(75, 85, 101, 0.6);
      }
    }

    .header-content {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 0.5rem;
    }

    tbody {
      background-color: rgb(207, 207, 207);
      color: black;

      tr {
        border-bottom: 1px solid rgba(255, 255, 255, 0.05);
        transition: background-color 0.2s ease;

        &:hover {
          background-color: rgba(55, 65, 81, 0.3);
        }

        &:last-child {
          border-bottom: none;
        }
      }

      td {
        color: black;
        font-size: 1.5rem;
      }
    }

    .status-badge {
      display: inline-block;
      padding: 0.25rem 0.75rem;
      border-radius: 9999px;
      font-size: 0.875rem;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: 0.05em;
    }

    .btn-primary {
      background-color: #007bff;
      border: 1px solid #007bff;
      color: white;
      font-weight: 500;
      padding: 0.6rem 1rem;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      font-size: 1.4rem;

      &:hover {
        background-color: transparent;
        color: #007bff;
      }
    }

    .no-results {
      text-align: center;
      padding: 20px;
      color: black;
      font-size: 1.4rem;
    }
  }
}

.search-container2 {
  position: relative;

  input {
    padding: 1rem 1.2rem 1rem 2.5rem;
    border-radius: 8px;
    background-color: rgb(197 197 197 / 70%);
    border: 1px solid rgba(255, 255, 255, 0.1);
    color: black;
    font-size: 1.4rem;
    transition: all 0.3s ease;
    min-width: 250px;

    &:focus {
      outline: none;
      border-color: #6366f1;
      box-shadow: 0 0 0 2px rgba(99, 102, 241, 0.25);
    }

    &::placeholder {
      color: rgba(0, 0, 0, 1);
    }
  }

  .search-icon {
    position: absolute;
    left: 0.75rem;
    top: 50%;
    transform: translateY(-50%);
    color: rgba(209, 213, 219, 0.5);
  }
}

.table-wrapper {
  overflow-x: auto;
  background-color: rgba(31, 41, 55, 0.4);
  border-radius: 8px;
}

.pagination {
  &-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1.5rem;
    padding-top: 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);

    @media (max-width: 640px) {
      flex-direction: column;
      gap: 1rem;
    }
  }

  &-text {
    color: black;
    font-size: 1.3rem;
  }

  &-controls {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    flex-direction: row !important;
    gap: 0.5rem !important;
  }

  &-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    border-radius: 6px;
    background-color: rgba(55, 65, 81, 0.6);
    border: none;
    color: #e5e7eb;
    cursor: pointer;
    transition: all 0.2s ease;
    padding: 1.2rem;

    &:hover:not(.disabled) {
      background-color: rgba(75, 85, 101, 0.6);
    }

    &.disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }

  &-number {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    border-radius: 6px;
    background-color: rgba(55, 65, 81, 0.6);
    border: none;
    color: #e5e7eb;
    cursor: pointer;
    transition: all 0.2s ease;
    padding: 1.2rem;

    &:hover {
      background-color: rgba(75, 85, 101, 0.6);
    }

    &.active {
      background-color: #6366f1;
      color: white;
    }
  }
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-top-color: #6366f1;
  animation: spin 1s ease-in-out infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

@media (max-width: 768px) {
  .client-panel {
    flex-direction: row;
  }

  .client-aside {
    width: 70px;
    min-width: 70px;
  }

  .client-dashboard {
    h1 {
      font-size: 1.8rem;
    }

    .small-box {
      .inner {
        h3 {
          font-size: 2rem;
        }

        p {
          font-size: 1rem;
        }
      }
    }

    .dashboard-charts {
      .chart-container {
        height: 350px;
      }
    }
  }

  .product-table {
    th,
    td {
      padding: 0.75rem;
    }

    th {
      font-size: 1.2rem;
    }

    td {
      font-size: 1.2rem;
    }
  }

  .btn-primary {
    padding: 0.5rem 0.75rem;
    font-size: 1.2rem;
  }

  .search-container2 input {
    min-width: 200px;
    font-size: 1.2rem;
  }
}

@media (max-width: 576px) {
  .client-panel {
    flex-direction: column;
  }

  .client-aside {
    width: 100%;
    min-width: 100%;
    height: auto;
    order: -1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 0.5rem;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

    &__nav {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
    }

    &__list {
      display: flex;
      flex-direction: row;
      width: 100%;
      justify-content: space-around;
      padding: 0;
      margin: 0;
      align-items: center;
      justify-content: flex-end;
    }

    &__toggle {
      display: none;
    }

    &__link {
      font-size: 2.2rem;
      padding: 0;
    }
  }

  .client-header {
    margin-top: 0px !important;
  }

  .client-wrapper {
    padding: 0.5rem;
  }

  .client-dashboard {
    h1 {
      font-size: 2rem;
    }

    .dashboard-charts {
      h2 {
        font-size: 2.8rem;
        color: black;
      }
    }
  }

  .client-main {
    padding: 0 !important;
  }
}

@media (max-width: 480px) {
  .product-table-container2 {
    padding: 1rem;
  }

  .product-table {
    th,
    td {
      padding: 0.5rem;
    }

    th {
      font-size: 1rem;
    }

    td {
      font-size: 1rem;
    }
  }

  .btn-primary {
    padding: 0.4rem 0.6rem;
    font-size: 1rem;
  }

  .search-container2 input {
    min-width: 100%;
    font-size: 1rem;
  }
}

@media (max-width: 750px) {
  .client-complaints {
    max-width: none;
  }

  .hide-complaints {
    display: none;
  }
}

@media (max-width: 475px) {
  .hide-complaints2 {
    display: none;
  }
}
